@import url('https://fonts.googleapis.com/css2?family=Sofia+Sans:wght@400;800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  font-family: 'Sofia Sans', sans-serif;
  font-size: 22px;
}

body {
  background-color: #1c2e4a;
}

body::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, rgb(255, 154, 139) 0%, rgb(255, 106, 136) 55%, rgb(255, 153, 172) 100%);
  clip-path: circle(30% at right 70%);
  z-index: -1;
}

body::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, rgb(251, 218, 97) 0%, rgb(255, 90, 205) 100%);
  clip-path: circle(20% at 10% 10%);
  z-index: -1;
}

.active {
  text-decoration: underline 2px #FF5ACD;
}

.flex {
  display: flex;
  /* width: 100vw; */
  margin: 40px 25px;
  justify-content: space-around;

}

.bold {
  font-weight: 800;
}

.current, .forecast {
  padding: 25px;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(10px);
}

.current {
  width: 30%;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.current__parametrs {
  display: flex;
  justify-content: space-evenly;
}

.forecast {
  width: 50%;
  border-radius: 15px;
}

.input {
  display: flex;
  justify-content: center;
  margin-top: 10px;

}

.input input {
  border: none;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-left: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.2);
  color: rgb(255, 255, 255);
}

.bk-bl {
  margin: 20px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-left: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.2);
  color: rgb(255, 255, 255);
  width: 30%;
  text-align: center;
  border-radius: 10px;
}

.clrwt{
  color: #f8f8f8;
}